body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{display:flex;justify-content:space-between;align-items:flex-start;max-width:1200px;margin:0 auto;padding:15px 0;width:100%}

.ItemHero{display:flex;justify-content:space-around;align-items:center;width:50%;border:2px solid #363636;border-radius:5px;margin:0 10px;padding:10px;background:#2a2a2a;color:#fff}.ItemHero__list{margin:0;padding:0;list-style:none;width:100%}.ItemHero__list li{border-top:2px solid #363636;padding:5px 0}.ItemHero__img{width:30%}.ItemHero__content{display:flex;justify-content:flex-start;align-items:center;flex-direction:column;margin-left:10px;width:calc(70% - 10px)}.ItemHero__title{margin:0 0 10px;width:100%;text-align:start}

.ListHeroes{list-style:none;margin:0 10px;padding:0;width:50%;height:100%;border:1px solid #363636;border-radius:5px}.ListHeroes li{padding:5px 10px;color:#fff;cursor:pointer;background:#2a2a2a;border:1px solid #363636}.ListHeroes li:hover{background:#3d3d3f}.ListHeroes li:first-child{border-radius:5px 5px 0 0}.ListHeroes li:last-child{border-radius:0 0 5px 5px}

.Loader{height:100vh;width:100%;display:flex;justify-content:center;align-items:center}.Loader-img{height:30vmin;pointer-events:none;-webkit-animation:lds-ripple 1s cubic-bezier(0, 0.3, 0.7, 1) infinite;animation:lds-ripple 1s cubic-bezier(0, 0.3, 0.7, 1) infinite}@-webkit-keyframes lds-ripple{0%{top:28px;left:28px;width:0;height:0;opacity:1}100%{top:-1px;left:-1px;width:58px;height:58px;opacity:0.3}}@keyframes lds-ripple{0%{top:28px;left:28px;width:0;height:0;opacity:1}100%{top:-1px;left:-1px;width:58px;height:58px;opacity:0.3}}

